	module.exports = {"data":{"post":{"html":"<p>The pop duo t.A.T.u. performed on \"Wetten Dass\" on Saturday, and kissed rather passionately on stage. ZDF turned the cameras away... because of the war in Iraq! A network spokesman was quoted in the <a title=\"Es gibt doch zwei Rudi Völler\" href=\"http://www.abendblatt.de/daten/2003/03/24/137560.html\">Abendblatt</a>. \"It wasn't appropriate for a family show. And the mood of the broadcast was set by the war. We wanted everything to be somewhat 'softer'.\" </p>\n<p>(Note the use of the 'New German' term 'softer' by the spokesman. As is usually the case with English words inserted directly into a German sentence, I have no idea what it's supposed to mean.)</p>","fields":{"title":"No Kisses In Times Of War","date":"24 Mar 2003","url":"https://papascott.de/2003-03-24-no-kisses-in-times-of-war/"}}},"pathContext":{"slug":"/2003-03-24-no-kisses-in-times-of-war/","prev":{"fields":{"title":"27 Hours Berlin","date":"24 Mar 2003","slug":"/2003-03-24-27-hours-berlin/"}},"next":{"fields":{"title":"Embedded","date":"25 Mar 2003","slug":"/2003-03-25-embedded/"}}}}

